/* MainCallBack */
.main__callback {
  max-width: 330px;
  width: 100%;
  padding: 28px 24px 28px;
  background: #f4f4f4;
  border-radius: 8px;
  position: relative;
}
.main__callback .main__callback-title {
  line-height: 28px;
  margin-bottom: 8px;
  font-size: 20px;
  text-align: center;
  text-transform: uppercase;
}
.main__callback #callbackform {
  padding: 0;
  border: 0px;
  border-bottom: 1px solid #DADADA;
  border-radius: 0;
}
.main__callback #callbackform .form__content {
  flex-direction: column;
}
.main__callback #callbackform .form__content label p {
  color: #7F808A;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}
.main__callback #callbackform .form__input {
  background-color: #F4F4F4;
  border-radius: 4px;
  border: 1px solid #DADADA;
  color: #19191c;
}
.main__callback #callbackform .form__input::placeholder {
  color: #7F808A;
}
.main__callback #callbackform .form__input:active, .main__callback #callbackform .form__input:focus {
  box-shadow: inset 0 0 0 3px #b98a6f;
}
.main__callback #callbackform .form__text {
  color: #19191c;
}
.main__callback #callbackform .form__btn {
  width: 100%;
  padding: 16px 50px;
  background-color: #19191c;
}
.main__callback #callbackform .form__btn:hover {
  background-color: #b98a6f;
}
.main__callback #callbackform .form__privacy {
  margin-top: 8px;
  text-align: center;
  position: relative;
  left: 0;
  bottom: initial;
  font-size: 12px;
}
.main__callback #callbackform .form__privacy a {
  font-size: 12px;
  text-decoration: underline;
}
.main__callback .main__callback-whatsapp p.form__text {
  margin-top: 20px;
  margin-bottom: 16px;
  color: #19191c;
  position: relative;
}
